.slider {
  height: 5px;
  position: relative;
  background: #e7e7e7;
  border-radius: 5px;
}

.slider .progress {
  height: 100%;
  left: 0;
  right: 0;
  position: absolute;
  border-radius: 5px;
  background: #c93e33;
}

.range-input {
  position: relative;
}

.range-input input {
  position: absolute;
  width: 100%;
  height: 5px;
  top: -5px;
  background: none;
  pointer-events: none;
  -webkit-appearance: none;
  -moz-appearance: none;
}
.range-input input .range-min {
  left: 0;
}
.range-input input .range-max {
  right: 0;
}

input[type=range]::-webkit-slider-thumb {
  height: 17px;
  width: 17px;
  border-radius: 50%;
  background: #c93e33;
  pointer-events: auto;
  -webkit-appearance: none;
  box-shadow: 0 0 6px rgba(0, 0, 0, 0.05);
  border: 2px solid white;
}

input[type=range]::-moz-range-thumb {
  height: 17px;
  width: 17px;
  border: 2px solid white;
  border-radius: 50%;
  background: #c93e33;
  pointer-events: auto;
  -moz-appearance: none;
  box-shadow: 0 0 6px rgba(0, 0, 0, 0.05);
}

.inputs {
  margin-bottom: 10px;
}/*# sourceMappingURL=slider.css.map */